import React, { useState } from 'react'
import { useEffect } from 'react';

// Assets and Styling
import "./../css/content.scss";
import cover from "./../imgs/cover.png";
import scold from "./../imgs/dripping_scold.png";
import plane from "./../imgs/plane_brother.png";

const Story = () => {
  const [opacity, setOpacity] = useState(false);
  const [imageOpacity, setImageOpacity] = useState(false);
  const [headerOpacity, setHeaderOpacity] = useState(false);

  useEffect(() => {
    setHeaderOpacity(true);
  }, [headerOpacity]);

  useEffect(() => {
    setOpacity(true);
  }, [opacity]);

  useEffect(() => {
    setImageOpacity(true);
  }, [imageOpacity]);

  return (
    <>
      <h1 className={headerOpacity ? "headerShow" : "textHide"}> The Story </h1>

      <img className={imageOpacity ? "secondImageFadeIn" : "hide"} src={cover} alt={"Cover of tinker tim"} />

      <p className={opacity ? "textShow" : "textHide"}>
      Tim is a young bear who <em>loves</em> to tinker. He makes planes that fly and potions that explode. </p>
      
      <img className={imageOpacity ? "show" : "hide"} src={plane} alt={"Tim flying a paper airplane for his brother"} />
      
      <p className={opacity ? "textShow" : "textHide"}>
      At first, Tim’s dad doesn’t understand his son’s tinkering and pushes Tim to do something productive instead, like learning an instrument. Tim's mom reminds his dad that he used to tinker too. In the end, Tim's dad comes around to encouraging Tim’s natural curiosity.
      </p>
      


      <img className={imageOpacity ? "show" : "hide"} src={scold} alt={"Tim's dad scolding Tim"} />


      <div className='line-breaks'></div>
    </>
  )
}

export default Story