import React, { useState, useEffect } from 'react'
import slingshot from "./../imgs/slingshot.png";

// Assets and Styling
import "./../css/content.scss";

const Inspiration = () => {
    const [opacity, setOpacity] = useState(false);
    const [imageOpacity, setImageOpacity] = useState(false);
    const [headerOpacity, setHeaderOpacity] = useState(false);
    const [secondHeaderOpacity, setSecondHeaderOpacity] = useState(false);
    const [secondTextOpacity, setSecondTextOpacity] = useState(false);

    useEffect(() => {
        setHeaderOpacity(true);
    }, [headerOpacity]);

    useEffect(() => {
        setOpacity(true);
    }, [opacity]);

    useEffect(() => {
        setImageOpacity(true);
    }, [imageOpacity]);

    useEffect(() => {
        setSecondHeaderOpacity(true);
    }, [secondHeaderOpacity]);

    useEffect(() => {
        setSecondTextOpacity(true);
    }, [secondTextOpacity]);

    return (
        <>
            <h1 className={headerOpacity ? "headerShow" : "textHide"}> Our Inspiration </h1>

            <p className={opacity ? "textShow" : "textHide"}>
                Through Tinker Tim, we reflect on the pressure we felt as kids to focus on “useful” activities like math classes and practicing the piano. Tinker Tim was written to encourage kids to tinker and be curious, and to encourage parents to support their kids’ natural curiosity.
            </p>

            <img src={slingshot} className={imageOpacity ? "show" : "hide"} alt={"Cover of tinker tim"} />

            <h1 className={secondHeaderOpacity ? "secondHeaderShow" : "secondHeaderHide"}> The Illustrator and Authors </h1>
            <p className={secondTextOpacity ? "secondTextShow" : "secondTextHide"}> Shiuan, the illustrator, is an interdisciplinary art student at the University of Washington who has worked on a variety of projects, including animation, ceramics, and other art. She's always wanted to illustrate a children's picture book, and we couldn't be happier with how it turned out.
            </p>
            <p className={secondTextOpacity ? "secondTextShow" : "secondTextHide"}> The authors are a group of four students at the University of Washington. Darren and Alex will be graduating this year, Kenny will be pursuing his Masters, and Aroosh will be entering his senior year. </p>

        </>
    )
}

export default Inspiration;